<style>
	*{
		margin: 0;padding: 0;
		list-style-type:none ;
	}
	.div{
		width: 400px;
		border: solid 1px red;
		background-color: #204D74;
		padding: 20px 0;
	}
	.div .div1{
		background-color: #204D74;color:#fff ;
	}
	ul li{
		background-color: #204D74;color:#fff ;
		line-height: 30px;
	}
</style>
<template>
	<div class="div">
		<div class="div1">
			<h3>myFistr（{{total}}）</h3>
		</div>
		<div>
			<ul>
				<li :v-bind:class="{done:i.completed}" v-for="(i,index) in togo">
					{{i.title}} / {{i.completed}}
					<button :class="i.completed?'btn-success':'btn-primary'" @click="toggle(index)">{{i.completed?'已完成':'完成'}}</button>   
				    <button @click="deleteItim(index)">删除</button>
				</li>
			</ul>
			<input v-model="newData.title" class="form-control" type="text" >
				    <!--点击添加newData-->
	       <button @click="addNew(newData)" class="btn btn-success">添加</button>
		</div>
		<div>
					
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
			newData:{id:null,title:"",completed:false},
					togo:[
					      {id:1,title:'下课',completed:false},
					      {id:2,title:'吃饭',completed:false},
					      {id:3,title:'睡觉',completed:false},
					      {id:4,title:'做梦',completed:false},
					      {id:5,title:'放学 ',completed:false},
					]
			}
	   },
	   methods:{//方法容器
					   addNew(data){//data是个参数
					   	this.togo.push(data)//this 就是这个参数内容 把这个内容添加到togo
					    this.newData ={id:null,title:'',
					    completed:false//代表是否完成状态
					    }
					    
					    console.log(this.togo)
					   },
					   toggle(index){//点击完成获取下标
					   	this.togo[index].completed = !this.togo[index].completed
					   	console.log(index);
					   },
					   deleteItim(index){
					   	this.togo.splice(index,1)//删除数组中的数据 从第几个开始，在第几个结束
					   }
				},
				
				computed:{//计算属性容器
					total(){
						return this.togo.length;
					}
				}
	}
</script>




