<template>
	<div class=""><!--相当于控制器 显示内容要在此id名下-->
			{{message}}
			<span v-text = "info"></span>
			<!--双向数据绑定-->
			<!--v-model = ""-->
			<input type="text" v-model="message"/>
			<!--输出的两种表达方式-->
			<!--一、{{ }}-->
			<!--二、v-text = "" 指令-->
		
			<hr />
			<!--循环数组显示-->
			<ul>
				          <!--获取下标 （i,自定义name）-->
				<li v-for = "(i,a) in lists">{{a}}--{{i.id}} -- {{i.title}}</li>
			</ul>
			<!-- 触发事件 -->
			<!--指令 v-on:click = 'go()'-->
			<input type="button" v-on:click="go()" value="点击" />
			<!--简写方法  @click = ""-->
			<button @click = "run()">点击二</button>
		</div>
</template>
<script>
	export default{
		              
				data(){
					return{
						message:'hell world',
					    info:'我是Vue',
						lists:[
					    {id:1,title:'class1'},
					    {id:2,title:'class2'},
					    {id:3,title:'class3'},
					    {id:4,title:'class4'},
					    {id:5,title:'class5'},
					    {id:6,title:'class6'}
					]
					
					}
				},
				methods:{//方法容器
					go(){
						alert('累了')
					},
					run(){
						alert('哭了')
					}
				}
			 }
			
</script>

<style>
	*{
		margin: 0;padding: 0;
		list-style-type: none;
	}
</style>


