import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Tianjia from '@/components/Tianjia'
import Learn from '@/components/Learn'
Vue.use(Router)

export default new Router({
  routes: [
//  {
//    path: '/',
//    name: 'Hello',
//    component: Hello
//  },
    {
    	path:'/',
    	name:'Learn',
    	component: Learn
    }
    ,
    {
    	path:'/tianjia',
    	component: Tianjia
    }
  ]
})



